import React from "react";
import config from "../config";
import "./AboutUs.css";
import atelier from "../assets/atelier.jpg";

const AboutUs = () => (
  <section className="about-us" id="about">
    <div className="about-us-container">
      {/* Left Image */}
      <div className="image-container">
        <img
          src={atelier} // Replace with the actual image path
          alt="About Us"
          className="about-us-image"
        />
      </div>

      {/* Right Text Description */}
      <div className="about-us-text-container">
        <h2>{config.aboutUsTitle}</h2>
        <p>
          La {config.siteName}, ne mndrim cu oferirea celor mai delicioase
          gogoi din ora. Folosim doar ingrediente de cea mai bun calitate i
          reete tradiionale pentru a aduce un zmbet pe faa fiecrui client.
        </p>
        <p>
          Echipa noastr dedicat lucreaz din greu pentru a asigura c fiecare
          gogo este perfect, iar serviciul nostru este de neegalat. Vino s
          ne vizitezi i s te bucuri de o experien culinar de neuitat!
        </p>
      </div>
    </div>
  </section>
);

export default AboutUs;
